
span {
    display: inline-block;
 }

.p-container{
    max-width: 600px;
    margin: 0 auto;
}

.top-btn{
    /* width: 50px; */
    margin-top: 23px;
    /* width: 30px; */
    /* height: 30px; */
    border-radius: 5px;
    padding: 3px 7px;
    background-color: #5F7EFF;
    color: white;
    border: none;
    font-weight: bold;
    font-size: 1.5rem;
    /* margin-right: 10px; */
    /* margin-left: 10px; */
    cursor: pointer;

}

.top-btn:hover, 
.top-btn:focus{
    background-color: #4D5B9E;
}



.p-q-container {
    margin: 0 20px;
    margin-top: 30px;
    padding: 10px 20px;
    padding-bottom: 20px;
    border-radius: 15px;;
    background-color:white;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.05);
}

/* ======== MATH ======== */
.bottom{
    vertical-align: sub;
    /* font-family: "Times New Roman"; */
    font-style:normal;
    font-size: 0.7em;
}

.center {
    text-align: center;
}
.math{
    /* font-family: "Times New Roman"; */
    font-family: CMSY10, CMEX10, Symbol, Times;
    font-style: italic;
    font-size: 1.3rem;
}

.math-num{
    /* font-family: "Times New Roman"; */
    font-family: CMSY10, CMEX10, Symbol, Times;
    /* font-style: normal; */
    font-size: 1.3rem;
    /* color: red; */
}

.frac{
    display: inline-flex; flex-direction: column; vertical-align: middle;
    text-align: center;
}

.shi {
    padding: 0 10px;
    border-bottom:
    1px solid #000000;
}
.bo{
    padding: 0 10px;
}

/* ================= */


.p-q-title-container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    margin-bottom: 0;
}

.p-img{
    max-width: 100%;
}

.p-question-title{
    margin-top: 5px;
    
}

.selection-raido-txt{
    display: inline;
}
.p-question-text{
    margin-top: 5px;
    line-height: 1.6em;
}

.p-title{
    margin-bottom: 0;
}

.p-label{
    margin: 10px;
    margin-left: 0;
    /* padding-left: 10px; */
    border-radius: 5px;
}

.p-correct{
    background-color: #94D7A2;
}

.p-wrong{
    background-color: #F8BCBC;
}

.fadeout{
    opacity: 0.5;
}

input{
    margin-right: 0.7em;
}

.btn{
    width: 150px;
    height: 50px;
    border-radius: 10px;
    border: none;
    background-color: #5F7EFF;
    color: white;
    font-weight: bold;
    cursor: pointer;
    font-size: 16px;
}

.btn-toppage{
    margin-top: 30px;
    margin-bottom: 20px;
    width: 250px;
    height: 50px;
    border-radius: 10px;
    border: none;
    background-color: #5F7EFF;
    color: white;
    font-weight: bold;
    cursor: pointer;
    font-size: 16px;
}

.btn:hover, 
.btn:focus{
    background-color: #4D5B9E;
}

.btn-container{
    margin-top: 30px;;
    text-align: center;
}

.result-container{
    display: flex;
    /* justify-content: flex-end; */
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* position: relative; */
    margin: 0 20px;
}

.result-txt{
    margin-top: 0;
    /* border: 1px solid red; */
    /* margin-right: 40px; */
}


.result-btn{
    /* position: absolute; */
    top: 0;
    right: 0;
}

.small-result-txt{
    font-size: 1.0em;
    color: gray;
}
.result-num{
    margin: 0;
    font-size: 2.0em;
    margin-left: 10px;
    margin-right: 10px;
}

.small-txt{
    font-size: 0.9em;
}

.very-small-txt{
    font-size: 0.5em;
}

.problem-title{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin: 0 20px;
    margin-bottom: 0;
}

.problem-title > img{
    margin-top: 20px;
    /* border: 1px solid pink; */

}

.check_icon{
    width: 40px;
}

.check_icon_small{
    width: 20px;
    margin-top: -20px;
    
}
@media screen and (min-width: 800px) { 
    .result-container{
        /* flex-direction: row; */
        /* position: relative; */
    }

    .result-btn{
        /* position: absolute; */
    }

    .btn-toppage{
        margin-top: 50px;
    }
}