body {
  margin: 0;
  /* font-family: 'Source Sans Pro', sans-serif; */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen','Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F5F7FB;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.footer-container{
    text-align: center;
    margin-top: 50px;
    /* margin-bottom: 10px; */
}

.footer-txt {
    color: rgb(170, 170, 170);
}


.cover-img {
    height: 200px;
    pointer-events: none;
    -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
            filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.top-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* font-size: calc(10px + 2vmin); */
}

.top-txt-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 40px;
    margin-bottom: 20px;
}

.top-title{
    font-size: 40px;
    margin-top: 0.7em;
    margin-bottom: 0;
}

.top-subtitle {
    margin-top: 1.0em;
    margin-bottom: 0;
    /* margin-top: 0.3em; */
    font-size: 18px;
    /* margin-bottom: 10px; */
}

.top-subtitle2 {
    margin-top: 0.2em;
    margin-bottom: 0;
    /* margin-top: 0.3em; */
    font-size: 18px;
    /* margin-bottom: 10px; */
}

.top-txt-container >a {
    text-decoration: none;
    color: #3B3936;

}

.top-chapter-container {
    /* display: flex; */
    /* flex-direction: column; */
    /* align-items: center; */

    margin-top: 0px;
    padding-top: 10px;
    padding-left: 20px;
    padding-right: 60px;
    padding-bottom: 10px;
    
    border-radius: 15px;
    max-width: 500px;
    background-color:white;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.05);

}



.chapter-face {
    display: flex;

    align-items: center;
    /* cursor: pointer; */
    /* margin-right: 100px; */
    margin-left: 0;
    /* font-weight: bold; */
    font-size: 1.2em;
}

.chapter-num:hover {
    color: #4D5B9E;
    border-bottom: 1px solid #4D5B9E;
}

.chapter-num {
    color: #3B3936;
    margin: 0.6rem 0;
    /* border: 1px solid gray; */
    /* font-size: 2rem; */
    /* margin-bottom: 25px; */
    border-bottom: 1px solid gray;

}

.top-chapter-title{
    text-align: center;
}

.check_top-icon{
    width: 25px;
    margin-right: 10px;
}

.reward-btn{
    cursor: pointer;
}

#come-btn1{
    display: none;
}

@media screen and (min-width: 800px) { 
    #come-btn2{
        display: none;
    }

    #come-btn1{
        display: block;
    }
    .top-container {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        flex: 1 1;
    }

    .top-txt-container{
        margin-right: 50px;
    }

    .main-container{
        /* padding-top: 50px; */
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        
    }

    .footer-container{
        text-align: center;
        /* margin-top: 40px; */
        
    }
}


.App {
  text-align: center;
  color: #3B3936;
  
}




span {
    display: inline-block;
 }

.p-container{
    max-width: 600px;
    margin: 0 auto;
}

.top-btn{
    /* width: 50px; */
    margin-top: 23px;
    /* width: 30px; */
    /* height: 30px; */
    border-radius: 5px;
    padding: 3px 7px;
    background-color: #5F7EFF;
    color: white;
    border: none;
    font-weight: bold;
